.Home {
  position: relative;
  display: grid;
  grid-template-columns: 20rem auto 20rem;
  gap: 1rem;
}
.postSide {
  display: none;
}

@media screen and (max-width: 1200px) {
  .Home {
    grid-template-columns: 35% 50% 20rem;
    overflow-y: scroll;
  }
}
@media screen and (max-width: 768px) {
  .Home {
    grid-template-columns: 1fr;
  }

  .Home::-webkit-scrollbar {
    display: none;
  }
}
