.Profile{
    position: relative;
    display: grid;
    grid-template-columns: 18rem auto 20rem;
    gap: 1rem;
}
.Profile-center{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}


@media screen and (max-width) {
    .Profile{
        grid-template-columns: 35% 50% 20rem;
        overflow-y: scroll;
    }
    
}

@media screen and (max-width:768px){
    .Profile{
      grid-template-columns: 1fr;

    }

    .Profile::-webkit-scrollbar{
      display: none;

    }
}